<template>
  <div class="zq-wrap">
    <h2 class="title">{{ getTitle }}</h2>
    <div class="poem">
      <p>
        {{ poem.content }}
      </p>
      <p>
        「{{ poem.title }}」
        <span class="author">{{ poem.author }}</span>
      </p>
    </div>
    <div class="zq-items">
      <div
        class="zq-item"
        v-for="item in showData"
        :key="item.id"
        title="click me"
        @click="toPath(item.path)"
      >
        <p>{{ item.title }}</p>
        <p>{{ item.desc }}</p>
      </div>
    </div>
    <div class="wave"></div>
  </div>
</template>

<script>
const jinrishici = require('jinrishici')
export default {
  components: {},
  data() {
    return {
      titleData: [
        'Something better will happen',
        'To be a better man',
        "Is life always this hard or just when you're a kid?",
        'It is nice to be needed',
        'The future belongs to those who believe in the beauty of their dreams'
      ],
      poem: {
        content: '相逢祗有梦魂间，可奈梦随春漏短，不到江南。',
        title: '浪淘沙·莫上玉楼看',
        author: '韩疁'
      },
      showData: [
        {
          id: 1,
          title: 'BLOG',
          desc: 'Write Sth Down',
          path: 'blog'
        },
        {
          id: 2,
          title: 'Projects',
          desc: 'Sth I Coded',
          path: 'projects'
        }
      ]
    }
  },
  computed: {
    getTitle() {
      const order = Math.floor(Math.random() * 5)
      return this.titleData[order]
    }
  },
  watch: {},
  mounted() {
    this.initRhthy()
  },
  methods: {
    initRhthy() {
      jinrishici.load(result => {
        // 自己的处理逻辑
        if (result.status !== 'success') {
          return
        }
        this.poem = {
          content: result.data.content,
          title: result.data.origin.title,
          author: result.data.origin.author
        }
      })
    },
    // 跳转
    toPath(path) {
      window.location.pathname = path || '/'
    }
  }
}
</script>
<style lang="scss" scoped>
@keyframes rotate {
  0% {
    transform: translate(-50%, 0) rotateZ(0deg);
  }
  50% {
    transform: translate(-50%, -2%) rotateZ(180deg);
  }
  100% {
    transform: translate(-50%, 0%) rotateZ(360deg);
  }
}
.zq-wrap {
  position: relative;
  height: 100%;
  padding: 10px;
  z-index: 3;
  .title {
    padding-top: 60px;
  }
  .poem {
    position: absolute;
    top: 150px;
    writing-mode: vertical-lr; /*从左向右 从右向左是 writing-mode: vertical-rl;*/
    writing-mode: tb-lr;
    direction: ltr;
    letter-spacing: 2px;
    p {
      line-height: 35px;
    }
  }
  .author {
    background: #a77f80;
    color: #e2e4e1;
    font-size: 0.8em;
    padding: 5px 3px;
    border-radius: 6px;
    letter-spacing: 1px;
  }
  .zq-items {
    text-align: right;
    display: inline-block;
    position: absolute;
    right: 30px;
    top: 25%;
    .zq-item {
      position: relative;
      z-index: 11;
      display: inline-block;
      height: 110px;
      color: #fff;
      background-color: #7d8971;
      margin-right: 45px;
      padding: 15px;
      border-radius: 80% 2px 60% 53% / 60% 2px 80% 53%;
      box-shadow: 3px 3px 5px #8a95a7;
      cursor: pointer;
      transition: all ease 0.5s;
      p:first-child {
        font-weight: 700;
      }
      p:last-child {
        margin-top: 20px;
        color: #e2e4e1;
      }
      &:hover {
        transform: scale(1.1);
      }
    }
  }
  .wave {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    &::before,
    &::after {
      content: '';
      position: absolute;
      left: 50%;
      min-width: 300vw;
      min-height: 300vw;
      background-color: #e2e7e1;
      -webkit-animation-name: rotate;
      animation-name: rotate;
      -webkit-animation-iteration-count: infinite;
      animation-iteration-count: infinite;
      -webkit-animation-timing-function: linear;
      animation-timing-function: linear;
      z-index: -1;
      box-sizing: border-box;
      border-width: 0;
      border-style: solid;
      border-color: #7d8971;
    }
    &::before {
      bottom: 27vh;
      border-radius: 45%;
      animation-duration: 10s;
    }
    &::after {
      bottom: 20vh;
      opacity: 0.5;
      border-radius: 47%;
      animation-duration: 10s;
    }
  }
}
</style>



